














































import {
  Component,
  Vue
} from 'vue-property-decorator';
import {
  Getter
} from 'vuex-class';

@Component
export default class HeaderButton extends Vue {
  @Getter filialName
  @Getter nameUser
  @Getter userType

  private menu = false;
  public logout(): void {
    window.localStorage.removeItem('token');
    this.$router.push('/');
  }
}
